import { defineComponent } from "@vue/composition-api";

export default defineComponent({
	name: "Goals",
	data() {
		return {
			input: `
---

Réaliser une interface basée sur l’API Rick And Morty

Stack technique: _VUEJS (2)_, _TYPESCRIPT_, _VUE ROUTER_ , _VUEX_, _SCSS_.

API utilisée: https://rickandmortyapi.com/documentation

---

#### 1 . Liste des cartes

* route: /characters

	* La barre de recherche doit permettre de filtrer sur le nom de la carte au niveau de la vue (Pas d’appel back au clic sur une carte).
	* Mettre en place un loader lors du premier chargement des données
	* Mettre en place une pagination.

#### 2 . Détail de la carte

* route: /characters/:id

	* Afficher les infos sur le personnage.
	* Gérer pour une navigation fluide
	* Mettre en place un loader lors du premier chargement des données

##### Critères:

- Clarté du code
- Structuration du code
- Gestion du data flow
- Pagination et fluidité
			`,
		};
	},
	computed: {
		compiledMarkdown: function() {
			return (window as any).marked(this.input, { sanitize: true });
		},
	},
	methods: {
		update(e: any) {
			this.input = e.target.value;
		},
	},
});
